.journey {
  width: 100%;
  padding: 100px 20px;
  .journey__container {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 40px;
    @media (max-width: 768px) {
      flex-direction: column-reverse;
    }
    img {
      max-width: 553px;
      height: 450px;
      object-fit: cover;
      border-radius: 10px;
      filter: drop-shadow(rgba(0, 0, 0, 1) 0px 0px 15px);
      transition: all .5s;
      &:hover{
      filter: drop-shadow(rgba(0, 0, 0, 1) 0px 0px 30px);
      cursor: pointer;
      }
      @media (max-width: 1024px) {
        max-width: 400px;
        height: 320px;
      }
      @media (max-width: 768px) {
        max-width: 340px;
        height: 270px;
      }
      @media (max-width: 425px) {
        max-width: 280px;
        height: 230px;
      }
    }

    .journey__context {
      display: flex;
      flex-direction: column;
      row-gap: 25px;
      max-width: 534px;
      h2 {
        @extend .global-h2;
        @media (max-width: 768px) {
          text-align: center;
        }
      }

      p {
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 1px;
        color: black;
        font-weight: 500;
        font-family: $fontText;
        @media (max-width: 768px) {
          text-align: center;
        }
      }
    }
  }
}
