.header {
  width: 100%;
  position: fixed;
  top: 0;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
  z-index: 1000;
  .header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
      img {
        @media (max-width: 1024px) {
          width: 100px;
        }
        @media (max-width: 425px) {
          width: 90px;
        }
      }
    }
    ul {
      display: flex;
      gap: 60px;
      margin-bottom: 10px;
      @media (max-width: 1024px) {
        margin-bottom: 0;
        position: absolute;
        width: 100%;
        left: 0;
        flex-direction: column;
        align-items: center;
        top: 100px;
        background-color: rgb(4, 5, 50);
        position: absolute;
        height: 0;
        overflow: hidden;
        transition: all 0.5s;
      }

      li > a {
        color: #fff;
        font-size: 18px;
        font-family: $fontHead;
        text-align: right;
        transition: all 0.2s ease-in-out;
        display: inline-block;
        position: relative;
        color: #fff;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        &:hover::after {
          width: 100%;
        }
        &::after {
          content: "";
          position: absolute;
          left: 50%;
          bottom: -5px;
          width: 0;
          height: 3px;
          background-color: white;
          transition: 0.5s ease-in-out;
          box-shadow: 0px 0px 20px whitesmoke;
          text-shadow: 0px 2px 20px whitesmoke;
          transform: translateX(-50%);
        }
      }
      li > .active::after {
        width: 100%;
      }
    }
    .right-box {
      display: flex;
      align-items: center;
      column-gap: 15px;
      @media (max-width: 1024px) {
        display: none;
      }
      a:last-of-type {
        @extend .global-btn;
        padding: 16px 24px;
        border: 2px solid white;
        background-color: transparent;
        color: white;
        &:hover {
          background-color: white;
          color: black;
        }
      }
    }
    .right-burg {
      display: flex; 
      align-items: center;
      gap: 15px;
      .icon {
        display: none;
        @media (max-width: 1024px) {
          display: flex;
        }
      }
    }
  }
}
