$fontSize: 16;

$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1170;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

$mainColor: #000;
$mainBackground: #fff;
$fontText: "Inter";
$fontHead: "Plus Jakarta Sans";

.global-btn {
  padding: 16px 28px;
  font-size: 16px;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.5s;
  font-family: $fontHead;
  cursor: pointer;
  letter-spacing: 1px;
  font-weight: 500;
}

.global-h2 {
  font-size: 46px;
  font-weight: 600;
  font-family: $fontHead;
  color: black;
  line-height: 50px;
  @media (max-width: 768px) {
    font-size: 38px;
  }
}

body {
  color: $mainColor;
  font-size: $fontSize + px;
  background-color: $mainBackground;
  background-size: cover;
  background-repeat: no-repeat;

  &._lock {
    overflow: hidden;
  }
}

@mixin no-select {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

// .no-select {
//   @include no-select;
// }




.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.page {
  flex: 1 1 auto;
}

.container {
  max-width: 1440px;
  margin: auto;
}

html {
  scroll-behavior: smooth;
}

