.notFound{
  background-color: rgb(4, 5, 50);
   width: 100%;
   height: 100dvh;
    display: flex;
   .notFound__container{
    display: flex;
    align-items: end;
    justify-content: space-around;
    gap: 150px;
        .notFound__left-box{
            display: flex;
            flex-direction: column;
            align-items: center;
            p{
                color: white;
                font-size: 76px;
                margin-bottom: 20px;
                transform: rotate(-7deg);
            }
            span{
                color: white;
                font-size: 42px;
                transform: rotate(-7deg) translateX(10px);

            }
            img{
                width: 340px;
                margin-top: 50px;
            }
        }
        .notFound__right-box{
            position: relative;
            display: flex;
            justify-content: center;
            img:first-of-type{
                position: absolute;
                width: 130px;
                top: 100px;
            }
            
        }
   }
}