.community {
  position: absolute;
  top: -240px;
  background-color: rgb(34, 53, 221);
  padding: 48px 64px;
  border-radius: 10px;
  width: 88%;
  display: flex;
  align-items: center; 
  justify-content: space-between;
  gap: 20px;
  @media (max-width:1440px){
    top: -253px;
  }
  @media (max-width:1024px){
    display: none;
  }
  .community__title {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    row-gap: 20px;
    h3 {
      font-size: 30px;
      font-family: $fontHead;
      font-weight: 600;
      color: white;
      letter-spacing: 1px;
    }
    p {
      color: white;
      font-size: 18px;
      font-family: $fontText;
      text-align: left;
      font-weight: 400;
      letter-spacing: 1px;
      max-width: 800px;
      line-height: 20px;
      margin-right: 200px
    }
  }
  a {
    @extend .global-btn;
    width: 165px;
    border: 2px solid white;
    background-color: rgb(34, 53, 221);
    color: white;
    position: absolute;
    top: 36%;
    right: 50px;
    &:hover {
      background-color: white;
      color: black;
    } 
  }
}
