.expert-services {
  width: 100%;
  padding: 110px 20px;

  .expert-services__container {
    display: flex;
    flex-direction: column;
    row-gap: 25px;
    align-items: center;

    h2 {
      @extend .global-h2;
      text-align: center;
    }

    p {
      text-align: center;
      font-size: 18px;
      max-width: 710px;
      font-family: $fontText;
      line-height: 26px;
      color: black;
      letter-spacing: 1px;
      font-weight: 500;
    }

    .expert-services__content {
      width: 100%;
      display: flex;
      align-items: start;
      justify-content: space-around;
      gap: 30px;
      padding-top: 25px;
      @media (max-width: 1024px) {
        flex-direction: column;
        align-items: center;
      }
      .expert-services__content-item {
        display: flex;
        flex-direction: column;
        max-width: 376px;
        row-gap: 5px;
        transition: all .5s;
        cursor: pointer;
        &:hover{
          transform: translateY(-20px);
        }
        img {
          border-radius: 10px;
          max-width: 100%;
          height: 360px;
          object-fit: cover;
        }

        h3 {
          font-size: 26px;
          font-weight: 600;
          margin: 15px 0;
          font-family: $fontHead;
          color: black;
        }

        p {
          font-size: 17px;
          font-weight: 500;
          text-align: left;
          letter-spacing: 1px;
          font-family: $fontText;
          color: black;
        }
      }
    }
  }
}
