.web-temp {
  display: flex;
  flex-direction: column;
  .website-templates-most-watched {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 100px 30px;
    margin: 100px 0;
    a {
      width: 100%;
      max-width: 300px;
      height: 400px;
      display: flex;
      flex-direction: column;
      row-gap: 8px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        border-radius: 10px;
      }
      h3 {
        font-size: 24px;
        color: black;
      }
      p {
        font-size: 20px;
        color: black;
      }
    }
  }
  h5{
    margin: 30px 0 70px;
    align-self: center;
  }
}
