.footer {
  width: 100%;
  background-color: rgb(4, 5, 50);
  padding: 70px 20px;
  @media (max-width: 425px) {
    padding: 20px;
  }
  .footer__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 35px;

    @media (max-width: 768px) {
      justify-content: space-around;
      flex-wrap: wrap;
    }
    @media (max-width: 425px) {
      flex-direction: column;
      align-items: center;
      gap: 60px;
    }

    .footerimgnone {
      width: 120px;
      @media (max-width: 1024px) {
        display: none;
      }
    }

    .footer__context {
      display: flex;
      flex-direction: column;
      row-gap: 35px;

      @media (max-width: 425px) {
        row-gap: 25px;
      }
      h3 {
        font-family: $fontHead;
        font-size: 22px;
        font-weight: 600;
        letter-spacing: 1px;
        color: white;
        @media (max-width: 1024px) {
          font-size: 19px;
          text-align: center;
        }
      }

      ul {
        display: flex;
        flex-direction: column;
        row-gap: 15px;
        @media (max-width: 1024px) {
          align-items: center;
        }
        li > .active::after {
          width: 100%;
        }
        li {
          color: white;
          font-family: $fontText;
          letter-spacing: 1px;
          font-size: 17px;
          line-height: 20px;
          font-weight: 400;
          cursor: default;
          a {
            color: white;
            font-family: $fontText;
            letter-spacing: 1px;
            font-size: 17px;
            line-height: 20px;
            font-weight: 400;
            transition: all 0.2s ease-in-out;
            display: inline-block;
            position: relative;
            &:hover::after {
              width: 100%;
            }
            &::after {
              content: "";
              position: absolute;
              left: 50%;
              bottom: -5px;
              width: 0;
              height: 3px;
              background-color: white;
              transition: 0.5s ease-in-out;
              box-shadow: 0px 0px 20px whitesmoke;
              text-shadow: 0px 2px 20px whitesmoke;
              transform: translateX(-50%);
            }

            @media (max-width: 1024px) {
              font-size: 16px;
            }
            @media (max-width: 425px) {
              font-size: 14px;
            }
            .instafaceimg {
              width: 32px;
              height: 32px;
              margin: 4px;
              @media (max-width: 425px) {
                width: 26px;
                height: 26px;
              }
            }
            img {
              width: 40px;
              height: 40px;
              @media (max-width: 425px) {
                width: 32px;
                height: 32px;
              }
            }
          }
        }
      }
    }
    .footer__cont {
      ul {
        li {
          a {
            display: flex;
            align-items: center;
            column-gap: 5px;
            &:hover::after {
              width: 0;
            }
          }
        }
      }
    }
  }
}
