.discover {
  width: 100%;
  padding: 100px 20px 400px;
  @media (max-width: 1024px) {
    padding: 100px 20px 300px;
  }
  @media (max-width: 768px) {
    padding: 100px 20px 250px;
  }
  .discover__container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @media (max-width: 1024px) {
      flex-direction: column-reverse;
      row-gap: 125px;
      justify-content: center;
    }

    .discover__info {
      display: flex;
      flex-direction: column;
      gap: 30px;
      max-width: 533px;
      span {
        font-size: 20px;
        font-weight: 600;
        font-family: $fontText;
        letter-spacing: 1px;
        color: black;
      }

      h2 {
        @extend .global-h2;
      }

      p {
        font-size: 18px;
        line-height: 20px;
        letter-spacing: 1px;
        font-family: $fontText;
        color: black;
        font-weight: 400;
        max-width: 533px;
      }

      a {
        @extend .global-btn;
        max-width: 165px;
        border: 2px solid black;
        background-color: white;
        color: black;
        &:hover {
          background-color: #4169e1;
          color: white;
          border: 2px solid #4169e1;
        }
      }
    }

    .discover__images {
      display: flex;
      position: relative;
      width: 100%;
      max-width: 635px;
      max-height: 440px;
      @media (max-width: 1024px) {
        align-self: center;
        width: 570px;
        height: 450px;
      }
      @media (max-width: 768px) {
        max-width: 400px;
        height: 390px;
      }

      @media (max-width: 425px) {
        width: 300px;
        max-height: 300px;
      } 
      img {
        width: 100%;
        max-width: 304px;
        max-height: 400px;
        position: absolute;
        border-radius: 10px;
        transition: all .5s;
        cursor: pointer;
        &:hover{
          transform: scale(.93);
        }
        @media (max-width: 768px) {
          max-width: 224px;
          max-height: 330px;
          &:hover{
            transform: none;
          }
        }
        @media (max-width: 425px) {
          width: 170px;
          max-height: 220px;
        }
      }
      img:first-of-type {
        bottom: -70px;
        right: 0;
        @media (max-width: 425px) {
          bottom: 0;
        }
      }
      img:last-of-type {
        top: 0;
        left: 0;
      }
    }
  }
}
