.banner {
  width: 100%;
  padding: 100px 20px;
  background-color: rgb(240, 241, 253);
  .banner__container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 50px;

    @media (max-width: 1024px) {
      text-align: center;
      flex-direction: column;
    }
    .banner__info {
      display: flex;
      flex-direction: column;
      row-gap: 30px;
      max-width: 675px;
      h2 {
        @extend .global-h2;
        @media (max-width: 768px) {
          font-size: 38px;
        }
      }

      p {
        font-size: 18px;
        color: black;
        font-weight: 500;
        font-family: $fontHead;
        line-height: 20px;
        letter-spacing: 1px;
        max-width: 675px;
      }
    }

    .banner__btns {
      display: flex;
      align-items: center;
      gap: 15px;
      @media (max-width: 425px) {
        flex-direction: column;
      }

      a {
        @extend .global-btn;
        width: 165px;
        color: white;
        background-color: rgb(34, 53, 221);
        border: 2px solid rgb(34, 53, 221);

        &:hover {
          border: 2px solid rgb(16, 32, 176);
          background-color: rgb(16, 32, 176);
        }
      }
      a:first-child {
        background-color: white;
        border: 2px solid black;
        color: black;
        &:hover {
          border: 2px solid rgb(34, 53, 221);
          background-color: rgb(34, 53, 221);
          color: white;
        }
      }
    }
  }
}
