.choose {
  width: 100%;
  background-color: rgb(240, 241, 253);
  padding: 300px 20px 100px;
  @media (max-width: 768px) {
    padding: 50px 20px;
  }
  .chooser__container {
    position: relative;
    display: flex;
    flex-direction: column;

    img {
      top: -550px;
      position: absolute;
      width: 100%;
      max-height: 500px;
      max-height: 500px;
      border-radius: 10px;
      object-fit: cover;
      @media (max-width: 768px) {
        top: -230px;
        height: 360px;
        object-fit: cover;
      }
    }

    .chooser__title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid rgb(228, 231, 253);
      padding-bottom: 40px;
      gap: 15px;
      @media (max-width: 768px) {
        flex-direction: column;
        padding-top: 170px;
      }
      h2 {
        @extend .global-h2;
        font-size: 40px;
        @media (max-width: 768px) {
          font-size: 26px;
          line-height: 34px;
        }
        @media (max-width: 425px) {
          font-size: 22px;
        }
      }

      a {
        @extend .global-btn;
        border: 2px solid rgb(34, 53, 221);
        background-color: rgb(34, 53, 221);
        color: white;
        cursor: pointer;
        &:hover {
          border: 2px solid black;
          background-color: white;
          color: black;
        }
      }
    }

    .choose__context {
      display: flex;
      justify-content: space-between;
      margin-top: 50px;
      gap: 40px;
      @media (max-width: 1024px) {
        flex-direction: column;
        row-gap: 45px;
      }
      .choose__context-item {
        display: flex;
        flex-direction: column;
        row-gap: 15px;
        max-width: 410px;

        span {
          color: rgb(34, 53, 221);
          font-size: 18px;
          font-weight: 600;
          font-family: $fontText;
          letter-spacing: 1px;
        }

        h3 {
          font-weight: 600;
          font-size: 26px;
          font-family: $fontHead;
          letter-spacing: 1px;
          color: black;
        }

        p {
          font-size: 18px;
          line-height: 22px;
          font-family: $fontText;
          letter-spacing: 1px;
          color: black;
          font-weight: 400;
        }
      }
    }
  }
}
