.quality {
  padding: 100px 20px 55px;
  width: 100%;
  .quality__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    cursor: grab;
    &:active {
      cursor: grabbing; /* Երբ քաշում ես, կուրսորը կփոխվի */
    }
    h2 {
      @extend .global-h2;
      font-size: 30px;
      text-align: center;
    }

    p {
      max-width: 840px;
      text-align: center;
      font-size: 18px;
      color: black;
      font-weight: 400;
      font-family: $fontText;
      line-height: 20px;
      letter-spacing: 1px;
    }

    img {
      border-radius: 50%;
      width: 60px;
    }
    span {
      font-weight: 600;
      font-size: 16px;
      color: black;
      font-family: $fontText;
      line-height: 20px;
      letter-spacing: 1px;
    }
  }
}
