.contact {
  width: 100%;
  padding: 100px 20px;
  @media (max-width: 768px) {
    padding: 40px 20px;
  }
  .contact__container {
    display: flex;
    align-items: start;
    justify-content: space-evenly;
    gap: 40px;
    @media (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
    .contact__context {
      display: flex;
      flex-direction: column;
      row-gap: 35px;
      h2 {
        @extend .global-h2;
        font-size: 36px;
      }

      p {
        max-width: 480px;
        font-family: $fontText;
        font-weight: 500;
        letter-spacing: 1px;
        font-size: 18px;
        line-height: 20px;
        color: black;
      }

      ul {
        display: flex;
        flex-direction: column;
        row-gap: 15px;
        li {
          display: flex;
          align-items: center;
          column-gap: 15px;
          font-family: $fontHead;
          font-weight: 600;
          font-size: 18px;
          letter-spacing: 1px;
          color: black;
          span {
            color: black;
            font-family: $fontText;
            font-weight: 500;
            font-size: 17px;
            letter-spacing: 1px;
            color: black;
          }
        }
      }

      .contact__context-box {
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgb(19, 23, 57);
        border-top: 1px solid rgb(19, 23, 57);
        padding: 20px 0;
        @media (max-width: 1024px) {
          flex-direction: column;
          row-gap: 20px;
        }

        ul {
          display: flex;
          flex-direction: column;
          row-gap: 15px;
          width: 100%;
          margin-right: 10px;
          padding-right: 10px;
          &:first-of-type {
            border-right: 1px solid rgb(19, 23, 57);
            @media (max-width: 1024px) {
              border: transparent;
            }
          }
          @media (max-width: 768px) {
            margin-right: 0;
          }
          li {
            font-family: $fontHead;
            font-weight: 600;
            font-size: 18px;
            letter-spacing: 1px;
            color: black;
            a {
              cursor: pointer;
              font-family: $fontText;
              font-weight: 500;
              font-size: 17px;
              letter-spacing: 1px;
              color: black;
            }
          }
        }
      }

      .contact__context-last {
        display: flex;
        align-items: center;
        column-gap: 30px;
        h3 {
          font-family: $fontHead;
          font-size: 18px;
          letter-spacing: 1px;
          font-weight: 600;
          color: black;
        }

        .contact__context-last-imgs {
          display: flex;
          align-items: center;
          column-gap: 15px;
          a {
            img {
              width: 30px;
            }
          }
        }
      }
    }

    form {
      display: flex;
      flex-direction: column;
      row-gap: 40px;
      width: 100%;
      max-width: 500px;
      .contact__form {
        display: flex;
        flex-direction: column;
        row-gap: 7px;

        label {
          font-family: $fontText;
          font-weight: 600;
          letter-spacing: 1px;
          line-height: 20px;
          font-size: 16px;
          color: black;
          span {
            color: red;
            font-size: 20px;
            font-family: $fontText;
            font-weight: 500;
            line-height: 20px;
          }
        }
        input {
          border-radius: 6px;
          max-width: 500px;
          padding: 10px 12px;
          font-size: 16px;
          border: 1px solid #05060f;
          color: #05060f;
          font-family: $fontText;
          font-weight: 500;
          letter-spacing: 1px;
          line-height: 20px;
        }
        textarea {
          border-radius: 6px;
          padding: 10px 12px;
          font-size: 16px;
          border: 1px solid #05060f;
          color: #05060f;
          resize: none;
          font-family: $fontText;
          font-weight: 500;
          letter-spacing: 1px;
          line-height: 20px;
        }
      }

      .activeBtn {
        @extend .global-btn;
        max-width: 165px;
        border: 2px solid rgb(34, 53, 221);
        background-color: rgb(34, 53, 221);
        color: white;
        &:hover {
          border: 2px solid black;
          background-color: white;
          color: black;
        }
      }
      .passiveBtn {
        @extend .global-btn;
        max-width: 165px;
        border: 2px solid rgb(34, 53, 221);
        background-color: rgb(34, 53, 221);
        opacity: .7;
        color: white;
        cursor: default;
      }
    }
  }
}
