.partner {
  width: 100%;
  background-color: #f0f1fd;
  padding: 110px 20px 180px;
  @media (max-width: 1024px) {
    padding: 110px 20px;
  }
  .partner__container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 40px;
    @media (max-width: 1024px) {
      flex-direction: column;
      justify-content: center;
    }
    .partner__imgs {
      position: relative;
      width: 490px;
      height: 450px;
      filter: drop-shadow(rgba(0, 0, 0, 0.7) 0px 0px 15px);

      @media (max-width: 768px) {
        width: 370px;
        height: 330px;
      }
      @media (max-width: 425px) {
        width: 300px;
        height: 260px;
      }
      img {
        object-fit: cover;
        object-position: center;
        border-radius: 10px;
        cursor: pointer;
        transition: all 0.5s;
        width: 80%;
        height: 85%;
        &:hover {
          transform: translateY(-30px);
          z-index: 3;
        }
        @media (max-width: 768px) {
          transform: none !important;
          pointer-events: auto !important;
        }
      }
      img:first-of-type {
        position: absolute;
        top: 0;
        left: 0;
      }
      img:last-of-type {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: 2;
      }
    }

    .partner__context {
      display: flex;
      flex-direction: column;
      row-gap: 20px;
      h2 {
        @extend .global-h2;
        max-width: 600px;
      }

      p {
        font-size: 18px;
        max-width: 660px;
        color: black;
        letter-spacing: 1px;
        font-family: $fontText;
        line-height: 26px;
        font-weight: 500;
      }

      a {
        @extend .global-btn;
        max-width: 165px;
        border: 2px solid black;
        background-color: transparent;
        color: black;
        &:hover {
          background-color: black;
          color: white;
        }
      }
    }
  }
}
