.website-templates {
  width: 100%;
  padding: 100px 20px;
  .website-templates__container {
    h2{
        text-align: center;
        font-size: 40px;
        
    }
      .invitation-templates-most-watched {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 20px;
      a {
        width: 100%;
        max-width: 300px;
        height: 400px;
        display: flex;
        flex-direction: column;
        row-gap: 8px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            border-radius: 10px;
        }
        h3 {
          font-size: 24px;
          color: black;
        }
      }
    }
  }
}
