.story {
  width: 100%;
  padding: 150px 20px 100px;
  @media (max-width: 1024px) {
    padding: 100px 20px;
  }
  .story__container {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 20px;
    h2 {
      @extend .global-h2;
      max-width: 600px;
      text-align: center;
      @media (max-width: 768px) {
        font-size: 38px;
      }
    }

    p {
      text-align: center;
      font-size: 18px;
      max-width: 780px;
      color: black;
      letter-spacing: 1px;
      line-height: 20px;
      font-weight: 500;
      font-family: $fontText;
    }

    .story__context {
      padding-top: 50px;
      display: flex;
      flex-direction: column;
      row-gap: 40px;

      .story__context-item {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 95px;
        padding-bottom: 40px;
        width: 100%;
        @media (max-width: 1024px) {
          flex-direction: column;
          row-gap: 20px;
          text-align: center;
        }
        &:first-of-type {
          border-bottom: 1px solid grey;
        }
        img {
          filter: drop-shadow(rgba(0, 0, 0, 0.7) 0px 0px 15px);
          object-fit: cover;
          object-position: center;
          max-width: 480px;
          max-height: 315px;
          border-radius: 10px;
          width: 100%;
          transition: all .5s;
          &:hover{
            transform: scale(1.05);
            cursor: pointer;
          }
          @media (max-width: 425px) {
            height: 240px;
          }
        }

        .story__context-item-info {
          h3 {
            font-family: $fontHead;
            letter-spacing: 1px;
            font-size: 28px;
            font-weight: 600;
            margin: 15px 0;
            color: black;
            @media (max-width: 1024px) {
              text-align: center;
            }
          }

          p {
            font-family: $fontText;
            font-size: 18px;
            font-weight: 500;
            max-width: 440px;
            text-align: left;
            color: black;
            letter-spacing: 1px;
            line-height: 20px;
            @media (max-width: 1024px) {
              text-align: center;
            }
          }
        }
      }
    }
  }
}
