.showcase {
  width: 100%;
  padding: 100px 20px;
  .showcase__container {
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    align-items: center;
    text-align: center;

    h2 {
      @extend .global-h2;
    }

    p {
      font-size: 18px;
      max-width: 850px;
      font-family: $fontText;
      font-weight: 500;
      letter-spacing: 1px;
      line-height: 20px;
      color: black;
    }

    .showcase__content {
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      width: 100%;
      gap: 70px;
      @media (max-width: 768px) {
        flex-direction: column;
      }
      .showcase__content-box {
        position: relative;
        display: flex;
        justify-content: center;
        max-width: 400px;
        img {
          width: 100%;
          border-radius: 10px;
        }

        p {
          position: absolute;
          bottom: 50px;
          color: white;
          font-size: 26px;
          font-weight: 700;
          font-family: $fontText;
          letter-spacing: 1px;
          line-height: 33px;
        }
      }
    }
  }
}
