.question {
  background-color: rgb(240, 241, 253);
  padding: 0 20px;
  .question__container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    gap: 40px;
    padding: 100px 0;
    @media (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
    h2 {
      @extend .global-h2;
      max-width: 540px;
      @media (max-width: 1024px) {
        text-align: center;
      }
    }

    .question__context {
      width: 100%;
      max-width: 600px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      .question__context-item {
        display: flex;
        flex-direction: column;
        padding: 20px 30px;
        row-gap: 15px;
        border-bottom: 1px solid grey;
        h3 {
          display: flex;
          justify-content: space-between;
          align-items: center;
          column-gap: 30px;
          color: black;
          font-family: $fontHead;
          letter-spacing: 1px;
          font-size: 18px;
          font-weight: 500;
          line-height: 20px;
          img {
            width: 15px;
            height: 15px;
          }
          span {
            font-size: 24px;
            -webkit-text-stroke: 1px black;
          }
        }
        p {
          color: black;
          font-family: $fontText;
          letter-spacing: 1px;
          line-height: 20px;
          font-weight: 400;
          // display: none;
          font-size: 18px;
        }
      }
    }
  }
}
