.projects {
  width: 100%;
  padding: 100px 20px;
  .projects__container {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
    align-items: center;
    text-align: center;

    h2 {
      @extend .global-h2;
    }

    p {
      font-size: 18px;
      max-width: 850px;
      font-family: $fontText;
      font-weight: 500;
      letter-spacing: 1px;
      line-height: 20px;
      color: black;
    }

    .projects__actual,
    .projects__content {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 20px;
      @media (max-width: 768px) {
        flex-direction: column;
      }
      .projects__content-box {
        display: flex;
        justify-content: center;
        max-width: 350px;
        max-height: 400px;
        @media (max-width: 768px) {
          max-width: 550px;
          max-height: 350px;
        }
        img {
          width: 100%;
          border-radius: 10px;
          object-fit: cover;
        }
      }
    }
    .projects__actual {
      margin-top: 20px;
      .projects__content-box {
        max-width: 535px;
        max-height: 450px; 
        @media (max-width: 768px) {
          max-height: 350px;
        }
      }
    }
  }
}
