.lang-option {
  display: flex;
  align-items: center;
  background: rgb(4, 5, 50);
  color: white;
  border-radius: 8px;
  width: 50px;
  font-weight: 500;
  font-size: 14px;
  height: 35px;
  animation: shadow 1s forwards infinite alternate;
}

@keyframes shadow {
  from {
    box-shadow: 0px 0px 5px whitesmoke;
  }
  to {
    box-shadow: 0px 0px 12px whitesmoke;
  }
}
