.website-templates {
  width: 100%;
  padding: 100px 20px;
  .website-templates__container {
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    .website-templates-image-box {
      display: flex;
      align-items: center;
      justify-content: space-around;
      gap: 30px;
      flex-wrap: wrap;
      a {
        position: relative;
        width: 100%;
        max-width: 250px;
        display: flex;
        justify-content: center;
        cursor: pointer;
        img {
          width: 100%;
          height: 250px;
          object-fit: cover;
          object-position: center;
          transition: all 0.5s;
          border-radius: 10px;
          &:hover {
            transform: scale(1.1);
          }
        }
        span {
          position: absolute;
          bottom: 30px;
          color: white;
          font-size: 30px;
          font-weight: 600;
          text-shadow: 0px 0px 7px black;
        }
      }
    }
    > h3 {
      text-align: center;
      font-size: 50px;
    }
    .website-templates-most-watched {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      gap: 100px 30px;
      a {
        width: 100%;
        max-width: 300px;
        height: 400px;
        display: flex;
        flex-direction: column;
        row-gap: 8px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            border-radius: 10px;
        }
        h3 {
          font-size: 24px;
          color: black;
        }
        p {
          font-size: 20px;
          color: black;
        }
      }
    }
  }
}
