.main {
  width: 100%;  
  background-size: cover;
  background-position: center;

  .main__container {
    padding: 220px 20px 170px;
    background-color: rgba(12, 6, 59, 0.7);
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
    justify-content: center;
    align-items: center; 
  }
  h1 {
    font-size: 58px;
    font-weight: 600;
    text-align: center;
    max-width: 700px;
    line-height: 65px;
    color: white;
    font-family: $fontHead;
    @media (max-width: 768px) {
        font-size: 46px;
    }
     @media (max-width: 425px) {
        font-size: 38px;
    }
  }
  p {
    max-width: 750px;
    font-size: 18px;
    padding-bottom: 20px;
    line-height: 26px;
    text-align: center;
    color: white;
    font-family: $fontText;
    letter-spacing: 1px;
    font-weight: 500;
  }
  a {
    @extend .global-btn;
    border: 2px solid white;
    background-color: rgb(34, 53, 221);
    color: white;
    &:hover {
      background-color: white;
      color: black;
    }
  }
}
