.services {
  width: 100%;
  padding: 100px 20px;
  .services__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 80px;
    @media (max-width: 1024px) {
      .services__context:nth-child(2) {
        flex-direction: column;
      }
    }
    .services__context {
      width: 100%;
      display: flex;
      align-items: center;
      column-gap: 70px;
      justify-content: center;
      @media (max-width: 1024px) {
        flex-direction: column-reverse;
        row-gap: 20px;
      }
      .services__context-info {
        display: flex;
        flex-direction: column;
        align-items: start;
        row-gap: 30px;
        max-width: 720px;
        @media (max-width: 1024px) {
          align-items: center;
        }
        h3 {
          font-size: 40px;
          max-width: 600px;
          text-align: left;
          font-family: $fontHead;
          letter-spacing: 1px;
          font-weight: 600;
          color: black;
          @media (max-width: 768px) {
            text-align: center;
            font-size: 29px;
          }
        }

        p {
          text-align: left;
          font-size: 18px;
          font-weight: 500;
          font-family: $fontText;
          color: black;
          letter-spacing: 1px;
          line-height: 20px;
          max-width: 525px;
          @media (max-width: 1024px) {
            max-width: 720px;
            text-align: center;
          }
        }

        a {
          @extend .global-btn;
          border: 2px solid black;
          background-color: white;
          color: black;
          &:hover {
            background-color: #4169e1;
            border: 2px solid #4169e1;

            color: white;
          }
        }
      }

      img {
        object-fit: cover;
        object-position: center;
        width: 550px;
        height: 450px;
        object-fit: cover;
        border-radius: 10px;
        filter: drop-shadow(rgba(0, 0, 0, 1) 0px 0px 15px);
        transition: all 0.5s;
        &:hover {
          filter: drop-shadow(rgba(0, 0, 0, 1) 0px 0px 30px);
          cursor: pointer;
        }
        @media (max-width: 1024px) {
          width: 500px;
          height: 420px;
        }
        @media (max-width: 768px) {
          width: 380px;
          height: 300px;
        }
        @media (max-width: 425px) {
          width: 270px;
          height: 200px;
        }
      }
    }
  }
}
